import { canvasHeight, canvasWidth, context } from '../constants/constants';

export default class Background {
    constructor() {
        const moon = new Image();
        const star = new Image();
        const stars = [];
        for(let i = 0; i < 40; i++) {
            stars.push({
                image: star,
                x: Math.floor(Math.random() * (canvasWidth - 50) + 25),
                y: Math.floor(Math.random() * (canvasHeight - 50) + 25),
            });
        }
        moon.src = 'data:image/gif;base64,';
        star.src = 'data:image/gif;base64,R0lGODdhBwAHAHcAACH5BAkKAAAALAAAAAAHAAcAwgAAAJaWlnl5eb29vdvb2wAAAAAAAAAAAAMRCBraMqKBFyWT4OYwyAialgAAOw==';
        this.background = {
            moon: {
                image: moon,
                x: Math.floor(Math.random() * (canvasWidth - 500) + 250),
                y: Math.floor(Math.random() * (canvasHeight - 500) + 250),
            },
            stars: stars,
        };
    }

    draw() {
        context.clearRect(0, 0, canvasWidth, canvasHeight);
        context.fillStyle = '#242526';
        context.fillRect(0, 0, canvasWidth, canvasHeight);
        for(let i = 0; i < this.background.stars.length; i++) {
            context.drawImage(
                this.background.stars[i].image,
                this.background.stars[i].x,
                this.background.stars[i].y,
            );
        }
        context.drawImage(
            this.background.moon.image,
            this.background.moon.x,
            this.background.moon.y,
        );
    }
}